.wp-block-bsb-slider {
    * {
        box-sizing: border-box;
    }

    .bsbCarousel {

        .indicatorsWrapper {
            width: 100%;
            height: 100%;
            position: absolute;
        }

        .carousel-indicators {
            margin: 0;
            gap: 10px;
            width: fit-content;
            height: fit-content;
            position: absolute;
            align-items: center;
            top: auto;
            right: auto;
            bottom: auto;
            left: auto;

            &.top-left {
                top: 0;
                left: 5%;

                &.vertical {
                    top: 5%;
                    left: 0;
                }
            }

            &.top-center {
                top: 0;
                left: 50%;
                transform: translateX(-50%);
            }

            &.top-right {
                top: 0;
                right: 5%;

                &.vertical {
                    top: 5%;
                    right: 0;
                }
            }

            &.center-left {
                top: 50%;
                left: 0;
                transform: translateY(-50%);
            }

            &.center-center {
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            &.center-right {
                top: 50%;
                right: 0;
                transform: translateY(-50%);
            }

            &.bottom-left {
                bottom: 0;
                left: 5%;

                &.vertical {
                    bottom: 5%;
                    left: 0;
                }
            }

            &.bottom-center {
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
            }

            &.bottom-right {
                bottom: 0;
                right: 5%;

                &.vertical {
                    bottom: 5%;
                    right: 0;
                }
            }

            button {
                border: none;
                position: relative;
                overflow: hidden;
                transform: scale(0.8);
                margin: 0;
                box-sizing: border-box;
                opacity: 1.1 !important;

                &.active {
                    transform: scale(1);
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    box-sizing: border-box;
                    position: absolute;
                    left: 0;
                    top: 0;
                    padding: 0;
                }
            }

            &.image {
                margin-bottom: 0;
            }
        }

        .carousel-item {
            position: relative;
        }

        .carousel-caption {
            z-index: 1 !important;
            padding: 0px;
            height: calc(100% - 30%);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            .captionContent {
                position: absolute;
                width: 100%;
                max-width: 100%;
                text-align: center;

                &.top-left {
                    text-align: left;
                    top: 0;
                }

                &.top-center {
                    text-align: center;
                    top: 0;
                }

                &.top-right {
                    text-align: right;
                    top: 0;
                }

                &.center-left {
                    text-align: left;
                    top: 50%;
                    transform: translateY(-50%);
                }

                &.center-center {
                    text-align: center;
                    top: 50%;
                    transform: translateY(-50%);
                }

                &.center-right {
                    text-align: right;
                    top: 50%;
                    transform: translateY(-50%);
                }

                &.bottom-left {
                    text-align: left;
                    bottom: 0;
                }

                &.bottom-center {
                    text-align: center;
                    bottom: 0;
                }

                &.bottom-right {
                    text-align: right;
                    bottom: 0;
                }
            }

            .carousel-button {
                margin-top: 20px;

                a {
                    text-decoration: none;
                    font-size: 16px;
                    font-weight: 400;
                }
            }
        }

        .bsbButtonDesign {
            button {
                background: none;
                background-color: transparent !important;
                border: none;
                cursor: pointer;
            }

            &:hover {
                background: none;
            }

            .bsbArrowButton {
                display: flex;
                align-items: center;
                justify-content: center;

                .fas {
                    font-style: normal;
                    font-variant: unicase;
                }
            }

            &.arrowMouseEffect {
                button {
                    width: 50% !important;

                    .bsbArrowButton {
                        display: none;
                    }
                }
            }
        }

        .carousel-item {
            width: 100%;
            height: 100%;

            img {
                width: 100% !important;
                height: 100% !important;
                object-fit: cover;
            }
        }

        &.flip {

            .carousel-item-next:not(.carousel-item-start),
            .active.carousel-item-end,
            .carousel-item-prev:not(.carousel-item-end),
            .active.carousel-item-start {
                transform: rotateY(180deg);
            }

            &.vertical {

                .carousel-item-next:not(.carousel-item-start),
                .active.carousel-item-end,
                .carousel-item-prev:not(.carousel-item-end),
                .active.carousel-item-start {
                    transform: rotateX(180deg);
                }
            }
        }

        &.default {
            .carousel-indicators {
                gap: 10px;
            }
        }

        &.vertical {
            .carousel-item {
                overflow: hidden;
            }

            .carousel-item-next:not(.carousel-item-start),
            .active.carousel-item-end {
                transform: translateY(100%);
            }

            .carousel-item-prev:not(.carousel-item-end),
            .active.carousel-item-start {
                transform: translateY(-100%);
            }

            .carousel-control-prev,
            .carousel-control-next {
                width: 100%;
                height: 60px;
            }

            .carousel-control-prev-icon,
            .carousel-control-next-icon {
                transform: rotateZ(90deg);
            }

            .carousel-control-next {
                top: unset;
                bottom: 0;
            }

            .bsbButtonDesign {
                .bsbArrowButton {
                    transform: rotateZ(90deg);
                }
            }

            .arrowMouseEffect {
                .carousel-control-prev {
                    left: 0;
                    top: 0;
                    width: 100%;
                    height: 50%;
                }

                button.carousel-control-next {
                    bottom: 0;
                    right: 0;
                    width: 100%;
                    height: 50%;
                }

            }
        }

        &.mouseDrag {
            cursor: grabbing;
        }

        @media all and (max-width: 767px) {
            .carousel-item {
                img {
                    height: 100%;
                }
            }
        }
    }
}